import './App.css';


import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Create from './Pages/Create';
import FormDetails from './Pages/FormDetails';
import PartDetails from './Pages/PartDetails';
import CompletedForms from './Pages/CompletedForms';
import CompletedInspection from './Pages/CompletedInspection'
import HomePage from './Pages/HomePage';
import UserDefinedField from './Pages/UserDefinedField'
import Navbar from './Components/navbar/NavBar';
import Header from './Components/header/Header';
import Footer from './Components/footer/Footer';


function App() {
  return (
    <Router>
      <div className="App">
        <Navbar />
          <Switch>
          <Route exact path="/">      
              <Header />
              <Footer />
            </Route>
            <div className="gradient__bg">
            <Route exact path="/HomePage">      
              <HomePage />
            </Route>
            <Route path="/CompletedForms">
              <CompletedForms />
            </Route>
            <Route path="/CompletedInspection">
              <CompletedInspection />
            </Route>
            <Route path="/UserDefinedField">
              <UserDefinedField />
            </Route>
            <Route path="/create">
              <Create />
            </Route>
            <Route path="/forms/:id">
              <FormDetails />
            </Route>
            <Route path="/parts/:id">
              <PartDetails />
            </Route>
           
            </div>
          </Switch>
       
      </div>
    </Router>
    
  );
}

export default App;

